#masthead {
    position: relative;
    margin: 0;
    border-radius: 0;
    /*background: url("./wallpaper.jpg") no-repeat center;*/
    /*background-size: cover;*/
}

#masthead {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 400px;
}

#masthead-grid .name {
    font-size: 1.75em;
}

#semantic-ui-logo {
    margin-left: 1em;
    margin-bottom: 7px;
}

.bottom-aligned {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.right-aligned {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

@media only screen and (max-width: 700px) {
    #masthead {
        min-height: 300px;
    }

    #masthead-grid {
        position: relative;
        top: 100px;
    }

    #masthead-grid .name {
        font-size: 1em;
    }
}
#navbar {
    padding-top: 2em;
    padding-bottom: 2em;
    margin-left: 5em;
    border-bottom: none;
}

.dark {
    background: #1b1c1d;
}
#home-content {
    padding-top: 4em;
}
